.container{padding:20px;margin:100px auto 0;max-width:800px;background-color:#ebeff3;border-radius:8px;box-shadow:0 4px 8px rgba(0,0,0,.1);font-family:"DM Sans",sans-serif;transition:all .3s ease-in-out}@media(max-width: 768px){.container{max-width:95%;margin-top:60px}}.labelHeader{font-size:24px;font-weight:bold;margin-bottom:20px;color:#6734ff;text-align:center;font-family:"DM Sans",sans-serif}.inputGroup{display:flex;align-items:center;gap:10px;margin-left:90px}.label{font-weight:bold;white-space:nowrap}.select{width:200px;height:40px;padding:8px;border:1px solid #999;border-radius:8px;background-color:#fff;box-sizing:border-box;font-family:"Roboto",sans-serif;font-size:16px;color:#000;cursor:pointer}.select:focus{border-color:#6734ff;outline:none}@media(max-width: 600px){.inputGroup{flex-direction:column;align-items:flex-start}.select{width:100%}}.textarea{width:100%;height:300px;padding:12px;border:1px solid #ccc;border-radius:8px;margin-top:5px;resize:vertical;box-sizing:border-box;font-family:"Roboto",sans-serif;font-size:16px}.buttonContainer{display:flex;justify-content:center;margin-top:20px;gap:20px}.buttonAnalise{width:300px;margin-top:20px;padding:12px;background:linear-gradient(135deg, #6734ff 0%, #4b00e0 100%);color:#fff;font-weight:bold;border:none;border-radius:8px;cursor:pointer;font-size:15px;text-transform:uppercase;transition:all .3s ease-in-out}.buttonPrompt{width:300px;margin-top:20px;padding:12px;background:linear-gradient(135deg, #6734ff 0%, #4b00e0 100%);color:#fff;font-weight:bold;border:none;border-radius:8px;cursor:pointer;font-size:15px;text-transform:uppercase;transition:all .3s ease-in-out}.button:hover{background:linear-gradient(135deg, #4b00e0 0%, #6734ff 100%);transform:translateY(-2px);box-shadow:0 4px 10px rgba(0,0,0,.2)}.button:active{transform:scale(0.98)}.button:disabled{background:#ccc;cursor:not-allowed}.uploadContainer{display:flex;align-items:center;gap:20px}.fileInput{display:none}.uploadButton{background-color:#6734ff;color:#fff;font-weight:bold;padding:12px 18px;border-radius:8px;cursor:pointer;border:none;display:flex;align-items:center;gap:8px;font-size:14px;transition:all .3s ease-in-out;margin-top:20px;margin-left:90px}.uploadButton:hover{background-color:#4b00e0}.fileName{font-size:14px;color:#333;font-weight:bold}